"use client";

import { useState, useMemo } from "react";
import type { Book } from "@/lib/definitions";
import BookCard from "./BookCard";
import BookSearch from "./BookSearch";
import { Pagination } from "../ui/Pagination";
import { reviews, getAverageRating } from "@/lib/data";

const BOOKS_PER_PAGE = 10;

interface BookListProps {
  initialBooks: Book[];
  genres: string[];
}

export default function BookList({ initialBooks, genres }: BookListProps) {
  const [searchTerm, setSearchTerm] = useState("");
  const [genre, setGenre] = useState("all");
  const [sortBy, setSortBy] = useState("title");
  const [currentPage, setCurrentPage] = useState(1);

  const booksWithRatings = useMemo(() => {
    return initialBooks.map(book => ({
      ...book,
      averageRating: getAverageRating(book.id)
    }));
  }, [initialBooks]);


  const filteredAndSortedBooks = useMemo(() => {
    return booksWithRatings
      .filter((book) => {
        const matchesSearch =
          book.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
          book.author.toLowerCase().includes(searchTerm.toLowerCase());
        const matchesGenre = genre === "all" || book.genre === genre;
        return matchesSearch && matchesGenre;
      })
      .sort((a, b) => {
        if (sortBy === "title") {
          return a.title.localeCompare(b.title);
        } else if (sortBy === "year") {
          return b.year - a.year;
        } else if (sortBy === 'rating') {
          return b.averageRating - a.averageRating;
        }
        return 0;
      });
  }, [booksWithRatings, searchTerm, genre, sortBy]);

  const totalPages = Math.ceil(filteredAndSortedBooks.length / BOOKS_PER_PAGE);
  const paginatedBooks = useMemo(() => {
    const startIndex = (currentPage - 1) * BOOKS_PER_PAGE;
    return filteredAndSortedBooks.slice(startIndex, startIndex + BOOKS_PER_PAGE);
  }, [filteredAndSortedBooks, currentPage]);

  const handlePageChange = (page: number) => {
    setCurrentPage(page);
    window.scrollTo(0, 0);
  };

  return (
    <div>
      <BookSearch
        onSearch={setSearchTerm}
        onGenreChange={setGenre}
        onSortChange={setSortBy}
        genres={genres}
      />
      {paginatedBooks.length > 0 ? (
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-x-6 gap-y-10">
          {paginatedBooks.map((book) => (
            <BookCard key={book.id} book={book} />
          ))}
        </div>
      ) : (
        <div className="text-center py-16">
          <p className="text-lg text-muted-foreground">No books found.</p>
        </div>
      )}
      {totalPages > 1 && (
        <Pagination
          currentPage={currentPage}
          totalPages={totalPages}
          onPageChange={handlePageChange}
        />
      )}
    </div>
  );
}
