"use client";

import { Input } from "@/components/ui/input";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Search } from "lucide-react";

interface BookSearchProps {
  onSearch: (term: string) => void;
  onGenreChange: (genre: string) => void;
  onSortChange: (sortBy: string) => void;
  genres: string[];
}

export default function BookSearch({
  onSearch,
  onGenreChange,
  onSortChange,
  genres,
}: BookSearchProps) {
  return (
    <div className="mb-8 grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4">
      <div className="relative">
        <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
        <Input
          type="search"
          placeholder="Search by title or author..."
          className="pl-10"
          onChange={(e) => onSearch(e.target.value)}
        />
      </div>
      <Select onValueChange={onGenreChange} defaultValue="all">
        <SelectTrigger>
          <SelectValue placeholder="Filter by genre" />
        </SelectTrigger>
        <SelectContent>
          <SelectItem value="all">All Genres</SelectItem>
          {genres.map((genre) => (
            <SelectItem key={genre} value={genre}>
              {genre}
            </SelectItem>
          ))}
        </SelectContent>
      </Select>
      <Select onValueChange={onSortChange} defaultValue="title">
        <SelectTrigger>
          <SelectValue placeholder="Sort by" />
        </SelectTrigger>
        <SelectContent>
          <SelectItem value="title">Sort by Title</SelectItem>
          <SelectItem value="year">Sort by Published Year</SelectItem>
          <SelectItem value="rating">Sort by Average Rating</SelectItem>
        </SelectContent>
      </Select>
    </div>
  );
}
